.productdisplay{
    display: flex;
    
    margin:0px 170px;

}
.productdisplay-left{
    display: flex;
    gap: 17px;
}
.productdisplay-img-list{
    display: flex;
    flex-direction: column;
    gap: 16px;
}
.productdisplay-img-list img{
    height: 163px;
}
.productdisplay-main-img{
    width: 586px;
    height: 700px;
}
.productdisplay-right{
    margin: 0px 70px;
    display: flex;
    flex-direction: column;
}
.productdisplay-right h1{
     font-weight: 700;
     color: #3d3d3d;
     font-size: 40px;
}
.productdisplay-right-star{
     display: flex;
     align-items: center;
     margin-top: 13px;
     gap: 5px;
     color: #1c1c1c;
     font-size: 16px;
}
.productdisplay-right-prices{
     display: flex;
     margin: 40px 0px;
     gap: 30px;
     font-size: 23px;
     font-weight: 700;
}
.productdisplay-right-prices-old{
     color: #818181;
     text-decoration: line-through;
}
.productdisplay-right-prices-new{
    color: #ff4141;
}
.productdisplay-right-size h1{
    margin-top: 55px;
    color: #656565;
    font-size: 25px;
    font-weight: 600;

}
.productdisplay-right-sizes{
    display: flex;
    margin: 30px;
    gap: 20px;
}
.productdisplay-right-sizes div{
    cursor: pointer;
    background: #fbfbfb;
    border: 1px solid #ebebeb;
    border-radius: 3px;
    padding: 18px 24px;
   
}
.productdisplay-right button{
    padding: 20px 40px;
    width: 200px;
    font-size: 20px;color: white;
    font-weight: 600;
    background: #ff4141;
    margin-bottom: 40px;
    border: none;
    outline: none;
    cursor: pointer;
     
}
.productdisplay-right-category{
    margin-top: 10px;
    font-size: 20px;
}
.productdisplay-right-category span{
    font-weight: 600;
}