.footer{
     display: flex;
     flex-direction: column;
     align-items: center;
     gap: 20px;
     justify-content: center;
}.footer-logo{
   
 display: flex;
  align-items: center;
 justify-content: center;
 gap: 20px;

 font-size: 30px;
}
.footer-logo p{
     color: #383838;
     font-size: 46px;
     font-weight: 700;
}

.footer-links {
     display: flex;
     align-items: center;
     list-style: none;
     gap: 50px;
     font-size: 20px;color: #252525;;
     
}
.footer-links li{
     cursor: pointer;
}
.footer-social-icon{
     display: flex;

     cursor: pointer;
     
     gap: 20px;
}
.footer-icons-container{
     padding: 10px;
     background: #fbfbfb;
     padding-bottom: 10px;
     border: 1px solid #ebebeb;
}
.footer-copyright {
    display: flex;
    flex-direction: column;
     color: black;
     align-items: center;
     justify-content: center;
     
    gap: 30px;
    width:100%;
    margin-bottom: 30px;
    color: #1a1a1a;
    font-size: 25px;
}
.footer-copyright hr{
    width: 80%;
   
   background-color: #171717;
    border-radius: 20px;
  
}